@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#wrapper{
  display:flex;
}
* {
  margin:0;
  padding:0;
  font-family:'Lato', sans-serif;
} 
*,
*::before,
*::after{
    box-sizing:border-box;
}
.home{
  background-image:url(/static/media/home_background.4431188f.png);
  background-color:#303030;
  background-color:var(--clr-dark);
  background-size:800px;
  background-blend-mode:soft-light;
  color:#fff;
  color:var(--clr-light);
  text-align:center;
}
.wrapper {
  position:relative;
  padding-top:3em;
  background-color: rgba(65, 42, 42, 0.5);
  box-shadow: 3px 3px 60px #a79d9d;
}
/*custom properties;update if needed*/
:root{
  --ff-primary: 'Source Sans Pro', sans-serif ;
  --ff-secondary: 'Source Code Pro', monospace;

  --fw-reg:300;
  --fw-bold:900;

  --clr-light:#fff;
  --clr-dark:#303030;
  --clr-accent:#00ffff;

  --fs-h1:3rem;
  --fs-h2:2rem;
  --fs-h3:1.5 rem;
  --fs-body:1rem;

  --bs:0.25em 0.25em 0.75em rgba(0,0,0,.25),
  0.125em 0.125em 0.25em rgba(0,0,0,.15);
}
/*general styles*/
html{
  scroll-behavior:smooth;
}
body{
  background-size:auto;
  background:#303030;
  background:var(--clr-dark);
  margin:0;
  font-family:'Source Sans Pro', sans-serif;
  font-family:var(--ff-primary);
  font-size:1rem;
  font-size:var(--fs-body);
  line-height:1.5;
  color:#fff;
  color:var(--clr-light);
}
section{
  padding:3em 2em;
}
img{
  display:flex;
  max-width:50%;
}
:focus{
  outline: 3px solid #00ffff;
  outline: 3px solid var(--clr-accent);
  outline-offset:3px;
}

h1,h2,h3{
  line-height:1;
  margin:0;
}
h1{
  font-size:3rem;
  font-size:var(--fs-h1);
}
h2{
  font-size:2rem;
  font-size:var(--fs-h2);
}
h3{
  font-size:1.5 rem;
  font-size:var(--fs-h3);
}

.logo{
  max-width:100px;
}

/*Intro*/
.intro{
  font-size:2rem;
  font-size:var(--fs-h2);
  position:relative;
  margin: 1em auto;
  text-align:center;
  justify-content:space-between;
}
.greeting{
  font-size:3rem;
  font-size:var(--fs-h1);
  align-items:center;
  text-align:center;
}
#yasol {
  display: inline-block;
  margin: 0 0.5rem;
  animation-name:bounce-5; /* referring directly to the animation's @keyframe declaration */
  animation-duration: 3s; /* don't forget to set a duration! */
  animation-timing-function:ease;
  color:gold;
  font-size:1em;
  cursor:-webkit-grab;
  cursor:grab;
}
#yasol:hover{
  transform: rotateY(180deg) translateZ(50px);
}
@keyframes bounce-5{
  0% {transform:scale(1,1)translateY(0);}
  30% {transform:scale(0.9,1.1)translateY(-100px);}
  50% {transform:scale(1.1,0.9)translateY(-7px);}
  70% {transform:scale(1.1)translateY(0);}
  100%{transform:scale(1.1)translateY(0);}
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
#cta{
  margin-top:2em;
  text-align:center;
  margin-bottom:2em;
}
#cta a {
  display: inline-block;
  height: 40px;
  width: 180px;
  color: #FFFFFF;
  border-radius: 100px;
  text-decoration: none;
  background-color:#303030;
  background-color:var(--clr-dark);
  font-size: 1.5em;
  font-weight:--fw-bold;
  box-shadow:6px 5px 8px #aaaaaa;
  transform:scale(1.3);
  transition:transform 200ms ease-in-out;
}

#cta:hover{
  transform:scale(1.1);
}
#cta a:hover {
  color: rgba(255, 198, 0, 0.85);
}
/*My services section*/
.mywork{
  display:block;
  background-color:#303030;
  background-color:var(--clr-dark);
  background-image:url(/static/media/work.f52c030b.jpg);
  background-size:800px;
  background-blend-mode:soft-light;
  color:#fff;
  color:var(--clr-light);
  text-align:center;
}

.portfolio_img{
  width:50%;
  height:50%;
  margin-right:auto;
  margin-left:auto;
}

.image_div{
  height:400px;
  width:400px;
  margin:3em 2em 3em 2em;
}
.image_div .work_image{
  float:right;
  margin-top:150px;
  display:inline-grid;
}
.portfolio_img{
  display:flex;
  width:50px;
  height:50px;
  margin:1em auto;
}
.portfolio_img:hover{
  display:inline-grid;
  width:60px;
  height:60px; 
  box-shadow:6px 5px 8px #aaaaaa;
  margin:1em auto;
}
.footer_container{
  background-color: #060b26;
  color:var(--accent);
  padding: 0.5em 0 0 0;
  font-size:1.5 rem;
  font-size:var(--fs-h3);
  text-align:center;
}
.footer a{
  color:inherit;
  text-decoration:none;
}

.footer_link:hover,.sns_list_link:hover{
  opacity:.7;
  box-shadow:6px 5px 8px #aaaaaa;
}
.sns_list_link{
  display:inline-flex;
  width:30px;
  height:30px;
}
.sns_list{
  list-style:none;
  display:flex;
  justify-content:center;
  margin:1em 0 0 0;
  padding:0;
}

.sns_list_link{
  margin:1em 2em 1em 2em;
}
.about_me{
  max-width:1000px;
  margin: 0 auto;
}
.about_me_img{
  box-shadow:0.25em 0.25em 0.75em rgba(0,0,0,.25),
  0.125em 0.125em 0.25em rgba(0,0,0,.15);
  box-shadow:var(--bs);
}
.myproject{
  background-color:#303030;
  background-color:var(--clr-dark);
  color:#fff;
  color:var(--clr-light);
  text-align:center;
}
.portfolio{
  color:rgb(187, 255, 50);
  font-weight:--fw-bold;
  text-decoration:none;
  display:inline-block;
}
.portfolio_img{
  transition:transform 750ms cubic-bezier(.5,0,.5,1) opacity 250ms linear;
}
.portfolio_title{
  text-decoration:none;
  position:relative;
  z-index:2;
}
.portfolio_img:hover,
.portfolio_img:focus .portfolio_img{
  transform:scale(1.25);
  opacity:0.5;
}

.portfolio_item_ind{
  padding:0 2em 2em;
  max-width:1000px;
  margin:0 auto;
  text-align:justify;
}
.portfolio_item_ind p{
  max-width:600px;
  margin-left:auto;
  margin-right:auto;
}
/* Resume Component*/
.resume_container{
  text-align:center;
  align-content:center;
  padding-top:20px;
}
.resume_heading{
  margin-left:10px;
  font-size:2rem;
  font-weight:--fw-bold;
  color:#00ffff;
  color:var(--clr-accent);
}
.resume_body{
  margin-top:10px;
}
.resume-title{
  font-weight:--fw-bold;
}
.resume_name{
  font-size:16px;
}
.resume_detail{
  font-size:0.9rem;
  padding-top:5px;
}
.resume_language_heading{
  font-size:--fs-h1;
  color:#fff;
  color:var(--clr-light);
  position:relative;
  text-align:center;
  font-size:2em;
}
.resume_language_heading{
  background-size:550px;
  background-blend-mode:soft-light;
  color:#fff;
  color:var(--clr-light);
  text-align:center;
  align-content:center;
}
.course_name{
  color:yellowgreen;
  height:70px auto;
  align-content:center;
}
.work_img{
  margin-top:2em;
  margin-left:auto;
  margin-right:auto;
  margin-bottom:0;
  height: 50px;
  width: 50px;

}
.resume_description{ 
  margin-top:1em;
  color:#fff;
  color:var(--clr-light);
  text-align:justify;
  margin-left:3em;
  margin-right:3em;
}
.skill_container{
  margin-top:2em;
  display:grid;
  grid-template-columns:auto auto auto auto;
  grid-gap:3em auto;
  margin-left:auto;
  margin-right:auto;
}
.skill_detail{
  margin-bottom:3em;
  text-align:center;
}
.skill_img{
  display:inline-grid;
  width:50px;
  height:50px;
}
.course_number, .course_name,
.skill_img:hover{
  transform:translate(60, 50);
  box-shadow:6px 5px 8px #aaaaaa;
  animation-name:rotate;
  animation-duration:1.5s;
}
.Navbar{
    background-color: #060b26;
    height:80px;
    display:flex;
    justify-content:start;
    align-items:center;
}
.menu-bars{
    margin-left: 2rem;
    font-size:2rem;
    background:none;
}
.nav-menu {
    background-color: #060b26;
    width:250px;
    height:350px;
    display:flex;
    position:absolute;
    top:0;
    left:-100%;
    transition: 1200ms;
    transform:ease-in-out;
    z-index:100;
}
.nav-menu.active{
    left:0;
    transition:600ms;
    transform:translateX(0%);
}

.nav-text{
    display:flex;
    justify-content:start;
    align-items:center; 
    padding:8px 0px 8px 16px;
    list-style:none;
    height:60px;
}
.nav-text a{
    text-decoration:none;
    color:#f5f5f5;
    font-size:18px;
    width:95%;
    height:100%;
    display:flex;
    align-items:center;
    padding: 0 16px;
    border-radius:4px;
    justify-content:space-evenly;
    flex-direction:column;
    justify-content:space-evenly;
}
.nav-text a:hover{
    background-color:var(--clr-dark);
    color:rgb(251, 255, 0);
    font-weight:--fw-bold;
    box-shadow:6px 5px 8px #aaaaaa;
    transform:scale(1.1);
}
.nav-menu-items{
    width:100%;
}
.navbar-toggle{
    background-color:#060b26;
    width:100%;
    height:70px;
    display:block;
    justify-content:start;
    align-items:center;
    margin-top:2em;
    margin-bottom:0em;
    cursor:pointer;
}
span{
    margin-left:16px;
}

/*Slider Carousel*/

.slider{
  padding-top:1em;
  max-width:600px;
  margin: auto;
  position:relative;
  height:100vh;
  display:flex;
  justify-content:center;
  align-items:center;
}
.image{
  display:inline-flex;
  position:relative;
  margin-bottom:2em;
  min-width:270px;
  height:300px;
  border-radius:10px;
}
.right-arrow{
  position:absolute;
  top:50%;
  right: 5%;
  width: 45px;
  height: 45px;
  transform: translateY(-30%);
  font-size:3rem;
  color:rgb(255, 255, 255);
  z-index:10;
  cursor:pointer;
  opacity:0.3;
  }
.left-arrow{
  position:absolute;
  top:50%;
  left:5%;
  width:45px;
  height: 45px;
  transform: translateY(-30%);  
  font-size:3rem;
  color:rgb(255, 255, 255);
  z-index:10;
  cursor:pointer;
  opacity:0.3;
} 
.slide{
  opacity:0;
  transition-duration: 3s ease-in-out;
  transform:translateZ(100px);
}
.slider, .active{
  opacity:1;
  transition-duration:1.8s;
  transform:scale(1.1);
}
.left-arrow:hover, .right-arrow:hover{
  transition-duration:1.8s;
  color:gold;
  animation:linear 30s;
  opacity:1;
}
.container{
    margin:100px auto;
    width:90%;
    box-shadow:1px 4px 1px rgba(211, 211, 211, 0.514);
    background-color:azure;
  }
  .container form{
    width:100%;
    padding:10px; 
  }
  .container label{
    display:block;
    letter-spacing:.1rem;
    font-style:italic;
    margin-bottom:3px;
  }
  .container input, .container textarea{
    width:100%;
    margin: 0 auto;
    padding:10px;
    border:none;
    box-shadow:1px 1px 4px 1px rgb(58,57,57);
  }
  .singleItem{
    margin:10px 0;
    color:rgb(0, 154, 165);
  }
  .btn{
      width:100%;
      text-align:center;
  }
  .btn button{
      width:150px;
      height:50px;
      font-size:18px;
      letter-spacing:0.2rem;
      padding:5px 10px;
      border: 1px solid transparent;
      outline:none;
      transition:all 0.5s ease-in-out;
      position:relative;
      margin-top:5px;
  }
  .btn button:hover{
      border:1px solid #fefefe;
      background-color:rgb(229, 255, 0);
      cursor:pointer;
  }
  input:invalid{
    box-shadow:5px 5px 5px #d17777;
    border: 5px solid #ff0000;
  }
  input:valid{
    box-shadow:5px 5px 5px #b0e6b0;
  }
  input:required{
    border-color:#800000;
    border-width:3px;
  }
